/*
  purgecss start ignore
  */

.snackbars {
  min-width: 280px;
}

.snackbar-action {
  color: inherit;
  padding: 8px 12px;
  margin-left: 4px;
  min-height: 40px;
  font-weight: 700;
  border-radius: 6px;
  border: 1px solid rgba(255, 255, 255, 0.35) !important;
}

.snackbar-action:hover:not([disabled]),
  .snackbar-action:focus:not([disabled]) {
    background-color: rgba(255, 255, 255, 0.16);
    border: 1px solid rgba(255, 255, 255, 0.35);
  }

.snackbar-action-primary {
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-black), var(--tw-text-opacity));
    background-color: #fff !important;
  }

.snackbar-action-primary:hover:not([disabled]), .snackbar-action-primary:focus:not([disabled]) {
      --tw-text-opacity: 1;
      color: rgba(var(--lc-color-black), var(--tw-text-opacity));
    }

.snackbar-action-primary:hover:not([disabled]),
    .snackbar-action-primary:focus:not([disabled]) {
      background-color: rgba(255, 255, 255, 0.85);
    }

.snackbar [class*='contentRoot'] {
    font-family: var(--lc-font-family-sans);
    font-size: 14px;
    line-height: 1.5;
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-white), var(--tw-text-opacity));
    --tw-bg-opacity: 1;
    background-color: rgba(var(--lc-color-gray-darker), var(--tw-bg-opacity));
    padding: 8px 16px;
    min-width: 280px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    border-radius: 6px;
  }

/*
  Note: Because of an unresolved Tailwind issue when using the `theme()` function with custom colors
  like we have them and because the `notistack` package sets `!important` on the `background-color` property
  for variants, we need to simply use our custom css theme variables for now.

  See: https://github.com/tailwindlabs/tailwindcss/discussions/2927
  */

.snackbar [class*='variantSuccess'] {
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-white), var(--tw-text-opacity));
    background-color: rgb(var(--lc-color-success)) !important;
  }

.snackbar [class*='variantSuccess'] .snackbar-action-primary {
      --tw-text-opacity: 1;
      color: rgba(var(--lc-color-success), var(--tw-text-opacity));
    }

.snackbar [class*='variantWarning'] {
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-white), var(--tw-text-opacity));
    background-color: rgb(var(--lc-color-warning)) !important;
  }

.snackbar [class*='variantWarning'] .snackbar-action-primary {
      --tw-text-opacity: 1;
      color: rgba(var(--lc-color-warning), var(--tw-text-opacity));
    }

.snackbar [class*='variantError'] {
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-white), var(--tw-text-opacity));
    background-color: rgb(var(--lc-color-danger)) !important;
  }

.snackbar [class*='variantError'] .snackbar-action-primary {
      --tw-text-opacity: 1;
      color: rgba(var(--lc-color-danger), var(--tw-text-opacity));
    }

.snackbar [class*='variantInfo'] {
    --tw-text-opacity: 1;
    color: rgba(var(--lc-color-white), var(--tw-text-opacity));
    background-color: rgb(var(--lc-color-primary)) !important;
  }

.snackbar [class*='variantInfo'] .snackbar-action-primary {
      --tw-text-opacity: 1;
      color: rgba(var(--lc-color-primary), var(--tw-text-opacity));
    }

.snackbar .SnackbarItem-message {
    padding: 10px 0 8px;
  }

/*
  purgecss end ignore
  */
